import { Observable } from 'rxjs/Observable';
import { Component, OnInit, Inject, Input, ComponentFactoryResolver, Injector } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { map } from 'rxjs/operators/map';
import { EbsPubSub } from '../models/ebsPubSub';
import { ActionSummary } from '../models/actionSummary';
import { EbsEvent } from '../../../../event-designer/views/eventDesigner/models/ebsEvent';
import { ActionComponent } from '../../../../action-designer/views/acmpdesigner/ccmpmetadata/ccmpmetadata';
import { GSPMetadataService, SelectMetadataComponent, MetadataDto } from '@gsp-lcm/metadata-selector';
import { BsModalService } from '@farris/ui';

@Component({
  selector: 'action-info',
  templateUrl: './actionInfo.html',
  styleUrls: ['./actionInfo.css']
})
export class ActionInfoComponent {
  @Input() action: ActionSummary;
  @Input() metadataDto:MetadataDto;
  constructor(private resolver:ComponentFactoryResolver,private modalService:BsModalService,private injector:Injector ){}
  //是否显示元数据弹框
  private showMetadataSelectDialog: boolean = false;
  //选中的元数据类型
  private selectedMetadataType: string;


  private openMetadataSelect() {
    //debugger;
    const compFactory=this.resolver.resolveComponentFactory(SelectMetadataComponent);
    const compRef=compFactory.create(this.injector);
    compRef.instance.metadataPath=this.metadataDto.relativePath;
    compRef.instance.typeName="Action";
    compRef.instance.selectedMetadata.subscribe($event => {
      // let selectedMetadata=$event;
      let action :ActionComponent= JSON.parse($event.metadata.content);
      if(!action.Code||!action.Id||!action.Name){
        alert("动作元数据信息有误，请检查！")
      }
      else{
        this.action.Code = action.Code;
        this.action.ID = action.Id;
        this.action.Name = action.Name
      }
      
    });
    const modalConfig = {
      title: '元数据选择',
      width: 882,
      height: 573,
      showButtons: false
    };
    const dialog = this.modalService.show(compRef, modalConfig);
    compRef.instance.closeModalEmitter.subscribe($event => {
      dialog.close();
    });
}





  //清空当前选中的元数据信息
  delete() {
    this.action.Code = '';
    this.action.Name = '';
    this.action.ID = '';

  }

  //打开元数据详情
  open() {

  }
}


