import {ComponentFactoryResolver, Injector, Type} from '@angular/core';
import { PubSubDesignerPackageView } from './views';
import { PubSubDesignerComponent } from './views/pubsubDesigner/pubsub.component';
import { Package, Workspace, GSP } from '@farris/ide-devkit';

export class PubSubDesignerPackage extends Package {

  private layoutPanel: any;

  constructor(
    gsp: GSP,
    private cfr: ComponentFactoryResolver,
    private injector: Injector
  ) {
    super(gsp);
  }


  activate(state) {
    //debugger;
    this.gsp.workspace.addOpener((uri: string) => this.openPubsub(uri));
  }

  openPubsub(uri) {
    //debugger;
    if (uri.endsWith('.pubsub')) {
      const pkgView = new PubSubDesignerPackageView({}, this.cfr, this.injector);
      const currentElement = pkgView.getElement().instance as PubSubDesignerComponent;
      currentElement.metadataUri = uri;
      // 这里需要处理uri，title应返回元数据名称，
      const fileName = uri.substring(uri.lastIndexOf('/') + 1);
      return {
        getTitle: () => fileName,
        getElement: () => pkgView.getElement()
      };
    }
  }

}


