import { FormsModule } from '@angular/forms';
import { NgModule } from '@angular/core';
import { HttpModule } from '@angular/http'
import { ButtonsModule } from '@progress/kendo-angular-buttons';
import { PubSubDesignerComponent } from './pubsub.component';
import { GridModule } from '@progress/kendo-angular-grid';
import { LayoutModule } from '@progress/kendo-angular-layout';
import { HttpClient, HttpClientModule, HttpClientJsonpModule } from '@angular/common/http';
import { DropDownsModule } from '@progress/kendo-angular-dropdowns';
import { ActionInfoComponent } from './actionInfo/actionInfo';
import { EventInfoComponent } from './eventInfo/eventInfo';
import { MappingInfoComponent } from './mappingInfo/mappingInfo';
import { BasicInfoComponent } from './basicInfo/basicInfo';
import { ToolBarModule } from '@progress/kendo-angular-toolbar';
import { CommonModule } from '@angular/common';
import { SelectMetadataComponent, MetadataSelectModule } from '@gsp-lcm/metadata-selector';
// import { SelectMetadataComponent } from '../../../bedesigner-frame/views/bedesigner-frame/refModule/metadata/selectmetadate/selectmetadata.component';
// import { MetadataModule } from '../../../udtdesigner-frame/views/udt-designer-frame/service/udt-reference';

@NgModule({
  declarations: [
    PubSubDesignerComponent,
    BasicInfoComponent,
    ActionInfoComponent,
    EventInfoComponent,
    MappingInfoComponent,
  ],
  imports: [
    FormsModule,
    HttpModule,
    CommonModule,
    HttpClientModule,
    HttpClientJsonpModule,
    ButtonsModule,
    GridModule,
    LayoutModule,
    DropDownsModule,
    ToolBarModule,
    MetadataSelectModule
  ],
  providers: [],
  entryComponents:[SelectMetadataComponent],
  bootstrap: [PubSubDesignerComponent]
})
export class PubSubDesignerModule { }



