import { EbsPubSub } from './models/ebsPubSub';
import { mockPubSub } from './mock/mockPubSub';
import { ActionSummary } from './models/actionSummary';
import { EventSummary } from './models/eventSummary';
import { MappingSummary } from './models/mappingSummary';
import { Component } from '@angular/core';
import { Response, Headers, RequestOptions, URLSearchParams, Http } from '@angular/http';
import { host } from './services/global';
import { GSPMetadataService, MetadataDto } from '@gsp-lcm/metadata-selector';
import { CacheService } from '@farris/ide-devkit/services';

@Component({
  selector: 'app-root',
  templateUrl: './pubsub.component.html',
  styleUrls: ['./pubsub.component.css']
})
export class PubSubDesignerComponent {
  public pubSub: EbsPubSub=new  EbsPubSub();

  constructor(private http: Http, private metadataService: GSPMetadataService, private cache: CacheService) {
    this.pubSub.Action=new ActionSummary();
    this.pubSub.Event=new EventSummary();
    this.pubSub.Mapping=new MappingSummary();
   }
  public metadataDto: MetadataDto;
  private _metadataContnet: MetadataDto;
  private _metadataUri: string;

  set metadataUri(metadataUri: string) {
    this._metadataUri = metadataUri;
    // todo: 通过元数据服务获取元数据内容
    const fileName = metadataUri.substring(metadataUri.lastIndexOf('/') + 1);
    const filePath = metadataUri.substring(1, metadataUri.lastIndexOf('/'));
    this.metadataService.LoadMetadata(fileName, filePath).subscribe(value => {
      this.metadataContent = value;
    });
  }

  set metadataContent(metadataContent: any) {
    //debugger;
    this._metadataContnet = metadataContent;
    this.loadMetadata(metadataContent);
  }

  //加载元数据
  private loadMetadata(dto: MetadataDto) {
    //debugger;
    this.metadataDto = dto;
    this.pubSub = this.getPubSubFromContent(this.metadataDto.content);
   
  }

  private getPubSubFromContent(content: string): EbsPubSub {
    let jsonStr = JSON.parse(content);
    let pubSubMetadata = new EbsPubSub;
    for (let key in jsonStr) {
      pubSubMetadata[key] = jsonStr[key];
    }
    if (!pubSubMetadata.Action) {
      pubSubMetadata.Action = {} as ActionSummary
    }
    if (!pubSubMetadata.Event) {
      pubSubMetadata.Event = {} as EventSummary
    }
    if (!pubSubMetadata.Mapping) {
      pubSubMetadata.Mapping = {} as MappingSummary
    }
    //debugger;
    return pubSubMetadata;
  }

  //保存元数据
  save() {
    //debugger;
    let stringcontent = JSON.stringify(this.pubSub);
    this.metadataDto.content = stringcontent;
    if (this.checkBeforeSave()) {
      let sessionId = this.cache.get('sessionId');
      this.metadataService.SaveMetadata(this.metadataDto, sessionId).toPromise().then(
        response => {
          if (response.status == 200) {
            alert("保存成功！");
          }
          else {
            alert("保存失败！");
          }
        }
      )
    }


  }

  checkBeforeSave() {
    if (!this.pubSub.Event.Id) {
      alert("事件信息不能为空！")
      return false;
    }
    if (!this.pubSub.Action.ID) {
      alert("动作信息不能为空！")
      return false;
    }

    if (this.pubSub.Description&&this.pubSub.Description.length > 2000) {
      alert("描述信息长度过长")
      return false;
    }
    return true;

  }
}



